.App {
  text-align: center;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}

* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

.heading_style {
  padding: 30px 0;
  text-align: center;
  font-family: "cursive";
  text-transform: uppercase;
  letter-spacing: 10px;
  font-weight: 500;
  font-size: 1.5rem;
  margin-bottom: 30px;
  background-color: aqua;
  color: black;
}
.cards {
  width: 100%;
  height: auto;
}
.heading {
  color: aqua;
}
.card {
  margin: 6%;
  transition: all 0.4s cubic-bezier(0.175, 0.885, 0, 1);
  background-color: white;
  width: 21.25;
  border-radius: 13px;
  box-shadow: 0px 14px 10px -7px rgba(0, 0, 0, 0.1);
  float: left;
}
.card_img {
  width: 100%;
  height: 235px;
  border-top-left-radius: 12px;
  border-top-right-radius: 12px;
  padding: 16px 24px 24px 24px;
  background-color: white;
}
.card_info {
  background-color: white;
  border-top-left-radius: 12px;
  border-top-right-radius: 12px;
  padding: 16px 24px 24px 24px;
}
.card_category {
  text-transform: uppercase;
  text-transform: uppercase;
  font-size: 13px;
  letter-spacing: 2px;
  font-weight: 500;
  color: gray;
  background-color: white;
}
.card_title {
  margin-top: 5px;
  margin-bottom: 10px;
  font-family: "cursive";
  background-color: white;
  text-transform: capitalize;
}
button {
  padding: 5px 8px;
  text-transform: uppercase;
  font-size: 14px;
  font-weight: 500;
  outline: none;
  border: 3px solid black;
  background-color: white;
  color: #868686;
  cursor: pointer;
}
